body {
  /* background: lightblue; */
  display: flex;
  justify-content: center;
}

.main-wrapper {
  /* border: 2px solid rgb(215, 148, 221); */
  width: 50vw;
}

.app__header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  border-bottom: 1px solid #dbdbdb;
  position: relative;
  /*  border: 2px solid rgb(17, 0, 255); */
}

.menu__avatar {
  object-fit: contain;
  width: 10px;
  height: 10px;
  /* border: 2px solid black; */
}

.input-search {
  position: absolute;
  right: 380px;
  top: 4px;
  outline: none;
  background: black;
}
input[type="search"] {
  width: 210px;
  height: 27px;
}

input:focus {
  outline: none;
}

#site-search::-webkit-input-placeholder {
  padding-left: 70px;
}

.menu__icons {
  display: flex;
  flex-direction: row;
  div {
    margin-left: 10px;
    color: #414141;
  }
  /* border: 2px solid red; */
}

.profile {
  /* border: 2px solid rgb(0, 0, 0); */
}

/* Main Channel section */

.MainChannelInfo__wrap {
  /*  border: 2px solid lightcoral; */
  display: flex;
  flex-direction: row;
}

.main-avatar {
  /* border: 2px solid rgb(255, 0, 0); */
  width: 320px;
}

.channel__info {
  /* border: 2px solid rgb(128, 148, 240); */
}

.user__subscribe__settings {
  /* border: 2px solid orange; */
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 620px;
  div {
    margin-right: 15px;
  }
  button {
    background: #0095f6;
    color: #fff;
    border: none;
    height: 30px;
    width: 62px;
    border-radius: 2px;
    font-size: 15px;
    font-weight: 700;
  }
  button:hover {
    cursor: pointer;
  }
  h1 {
    color: #545454;
  }
  .settings {
    cursor: pointer;
  }
}

.counters {
  /* border: 2px solid rgb(255, 0, 221); */
  display: flex;
  flex-direction: row;
  margin-top: -10px;
  span {
    margin-right: 20px;
  }
}

.channel__text {
  p {
    font-size: 18px;
    margin-top: -10px;
  }
  p:nth-child(1) {
    margin-top: 0;
  }
}

/* Posts section */

.posts__wrap {
  border-top: 1px solid #dbdbdb;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  /* background: rgb(0, 0, 0); */
  height: 50px;
}

.posts__menu__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  width: 410px;
  /* background: rgb(218, 154, 154); */
  height: 40%;
  li {
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;

    h2 {
      font-size: 14px;
      padding-left: 5px;
      color: #404040;
    }
    svg {
      color: #404040;
    }
  }
  li:hover {
    cursor: pointer;
  }
}

.posts__menu__list::before {
  content: "";
  position: absolute;
  width: 65px;
  height: 2px;
  bottom: 35px;
  background: #262626;
}
